import * as React from 'react'
import { Card} from 'antd'
import './card-elem.sass'
import {CheckCircleOutlined, StopOutlined} from "@ant-design/icons";

type AttrType = {
    title?: string
    headline: string
    compound?: number
    cluster?: number
    field?: string
    well?: number
}

const CardElem: React.FC<AttrType> = ({title,headline, compound, cluster, field, well}) => {

    return (
        <div className="card-elem">
            <Card size="small" title={title}>
                <h3 className="card-elem__headline">{headline}</h3>
                {compound === 1 ?
                    // eslint-disable-next-line react/jsx-no-undef
                    <p className={`card-elem__text card-elem__text--connect`}><b>:</b> {' '} <CheckCircleOutlined /></p>
                    :
                    // eslint-disable-next-line react/jsx-no-undef
                    <p className={`card-elem__text card-elem__text--no-connect`}><b>:</b> {' '} <StopOutlined /></p>
                }

                <p className="card-elem__text"><b>:</b> {cluster}</p>
                <p className="card-elem__text"><b>:</b> {field}</p>
                <p className="card-elem__text"><b>:</b> {well}</p>
            </Card>
        </div>
    )
}


export default CardElem